html{
    font-size: $small-font-size;
    font-family: $font-family;
  
    @media screen and (min-width:map-get($breakepoints, xs)){
    font-size: $base-font-size;
    }
    overflow: hidden;
    scroll-behavior: smooth;
  }
  strong{
    font-weight: 700;
  }
  
  h1{
    font-size: 1.5rem;
  }
  
  h2{
    font-size: 1.2rem;
    margin-bottom: $main-spacing / 2;
  }
  
  p{
    margin-bottom: $main-spacing / 2;
  }
  